/* eslint-disable */
<template>
 <div class="container">
    <h3>All Users</h3>
    <div class="container">
      <table class="table">
        <thead>
          <tr>
            <th>First Name</th>
            <th>Last Name</th>
            <th>Email Id</th>
            <th>Update</th>
            <th>Delete</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="user in result" v-bind:key="user.id">
            <td>{{ user.first_name }}</td>
            <td>{{ user.last_name }}</td>
            <td>{{ user.email_id }}</td>
            <td>
              <button class="btn btn-warning">
                <router-link :to="{ path: '/users/' + user.id }">Update </router-link> 
              </button>
            </td>
            <td>
              <button class="btn btn-danger" v-on:click="deleteUser(user.id)">
                Delete
              </button>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="row">
        <button class="btn btn-success" v-on:click="isOpenAddUserForm = true ">Add</button>
      </div>
    </div>
    <form @submit.prevent v-if="isOpenAddUserForm">
        <fieldset class="form-group">
          <label>First Name</label>
          <input type="text" class="form-control" required :value="first_name" @input="event => first_name = event.target.value"/>
        </fieldset>
        <fieldset class="form-group">
          <label>Last Name</label>
          <input type="text" class="form-control"  required :value="last_name" @input="event => last_name = event.target.value" />
        </fieldset>
        <fieldset class="form-group">
          <label>Email Id</label>
          <input type="text" class="form-control" required :value="email_id" @input="event => email_id = event.target.value"/>
        </fieldset>
        <button class="btn btn-success" @click="addUser()">Save</button>
      </form>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import { useRoute } from 'vue-router'

const route = useRoute()

let result = ref(undefined)
let isOpenAddUserForm = ref(false)
let id = route.query.id
let first_name = ref('')
let last_name = ref('')
let email_id = ref('')

onMounted(()=>{
  fetch('http://localhost/api')
    .then(response => {
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    return response.json();
  })
  .then(data => {
    console.log(data)
    result.value = data
    isOpenAddUserForm.value = false
  })
    .catch(error => {
    console.error('There was a problem with the fetch operation:', error);
  });
})

const deleteUser = (id)=> {
  fetch('http://localhost/api?id=' + id, {
  method: 'DELETE',
  headers: {
    'Content-Type': 'application/json'
  }
})
  .then(response => {
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    return response.json();
  })
  .then(data => {
    console.log(data);
    if(data.success){
      alert('Data was deleted')
    }  else if (!data.success){
      alert('Could not delete data')
    }
    fetch('http://localhost/api')
    .then(response => {
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    return response.json();
  })
  .then(data => {
    console.log(data)
    result.value = data
  })
    .catch(error => {
    console.error('There was a problem with the fetch operation:', error);
  });
  })
  .catch(error => {
    console.error('There was a problem with the fetch operation:', error);
  });

}

const addUser = (()=> {
 const body = {
  first_name : first_name.value,
  last_name :last_name.value,
  email_id: email_id.value
 }
 console.log(body)
  fetch('http://localhost/api', {
  method: 'POST',
  headers: {
    'Content-Type': 'application/json'
  },
  body: JSON.stringify(body)
})
  .then(response => {
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    return response.json();
  })
  .then(data => {
    console.log(data);
    first_name.value = ''
    last_name.value = ''
    email_id.value = ''
    isOpenAddUserForm.value = false
    fetch('http://localhost/api')
    .then(response => {
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    return response.json();
  })
  .then(data => {
    console.log(data)
    result.value = data
  })
    .catch(error => {
    console.error('There was a problem with the fetch operation:', error);
  });
  })
  .catch(error => {
    console.error('There was a problem with the fetch operation:', error);
  });
})


</script>
